.card {
  width: $card-size;
  box-sizing: border-box;
  padding: 32px 16px 16px;
  background-color: white;
  height: 408px;
  display: flex;
  flex-direction: column;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  font-weight: 400;

  &__title {
    font-size: 12px;
    font-weight: 500;
    line-height: 18px;
    margin-bottom: 0;
    color: $card-dark-color;
    transition: color 300ms;
  }

  &__button {
    background-color: $button-color;
    border-radius: 5px;
    width: 100%;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    color: white;
    text-transform: uppercase;
    font-size: 14px;
    font-weight: 700;
    text-decoration: none;
    line-height: 16px;

    transition:
      background-color 300ms,
      color 300ms;
  }

  &:hover {
    transform: scale(1.2);

    .card__title {
      color: $card-hover-title-color;
    }

    .card__button {
      background-color: #fff;
      border: 1px solid $button-color;
      color: $button-color;
      transition:
        background-color 300ms,
        color 300ms;
    }
  }

  &__image {
    background-image: url(../images/imac.jpeg);
    background-repeat: no-repeat;
    background-position: center;
    width: $image-width;
    padding-left: 19px;
    padding-right: 19px;
    height: $image-height;
    margin-bottom: 40px;
    background-size: 100% 100%;
    align-self: center;
  }

  &__code {
    color: $card-grey-color;
    font-size: 10px;
    font-weight: 400;
    margin-top: 4px;
    line-height: 14px;
  }

  &__reviews {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 16px;

    &-count {
      font-size: 10px;
      font-weight: 400;
      color: $card-dark-color;
      line-height: 14px;
    }
  }

  &__prices {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 16px;
    margin-top: 24px;
  }

  &__price-label {
    font-size: 12px;
    font-weight: 400;
    color: $card-grey-color;
  }

  &__price-value {
    font-size: 16px;
    font-weight: 700;
    line-height: 18px;
    color: $card-dark-color;
  }
}
